import { Component, OnInit } from '@angular/core';
import { AddDocExternComponent } from 'src/app/components/add-doc-extern/add-doc-extern.component';
import { environment as env } from 'src/environments/environment';
import { ModalController } from '@ionic/angular';
import { Funciones } from 'src/app/compartido/funciones';
import { ApiMongoService } from 'src/app/services/apiMongo.service';
import { ContractsService } from 'src/app/services/contracts.service';
import { GetCredComponent } from 'src/app/components/get-cred/get-cred.component';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-doc-extern',
  templateUrl: './doc-extern.component.html',
  styleUrls: ['./doc-extern.component.scss'],
})
export class DocExternComponent implements OnInit {

  list_main = [];
  pageOfItems: Array<any>;
  list = [];
  load = false;
  userSesion: any;

  key_table = '';

  nameFilter = '';
  orderBy = {
    key: '',
    order: true
  }

  thead = [
    { key: 'name', name: 'Titulo' },
    { key: 'data.document.iat', name: 'Fecha de emisión' },
    { key: 'data.document.subject.name', name: 'Titular' },
    /* { key: 'did', name: 'Cred. verificable' }, */
    { key: 'data.document.documentType.value', name: 'Tipo' },
    { key: 'status', name: 'Estado' },

  ];

  optionFilter = [
    { key: 'status', value: false, type: 'boolean' },
    { key: 'name', value: '', type: 'string' },
    { key: 'data.document.subject.name', value: '', type: 'string' },
  ];

  _env = env;

  /*GET  PARAMAS*/
  listDocType = [];
  parmasOk = false;

  constructor(
    private _auth: AuthService,
    private _contractsService: ContractsService,
    private _fun: Funciones,
    private _apiMongo: ApiMongoService,
    private _mod: ModalController) { }


  getParamas() {
    this._apiMongo._get(env.COLLECTION.general, env.TABLE_SIS.type_documents, 'items').subscribe((res: any) => {
      this.listDocType = res.result[0][env.TABLE_SIS.type_documents];
    })
  }

  ngOnInit() {
    this.key_table = env.TABLE_SIS.producer;
 

    this._auth.getUser().subscribe(async user => {
      if (!user) return;
      this.userSesion = user;
      this.key_table = env.TABLE_SIS.producer;
      this.getParamas();
      this.getList();
    });
  }

  segmentChanged(value) {
    if (this.load) return;// Corrigue erroes al cambiar de tabla rapidamente
    this.key_table = value.detail.value;
    this.list = [];
    this.getList();
  }

  async getList() {
    this.load = true;
    try {

      let res: any;
      if (this.userSesion.data.role.key == 'MASTER') {
        res = await this._apiMongo.filter(env.COLLECTION.document, env.TABLE_SIS.external, { 'data.partyType': this._fun.enum(this.key_table) });
      } else {
        res = await this._apiMongo.filter(env.COLLECTION.document, env.TABLE_SIS.external, { 'data.partyType': this._fun.enum(this.key_table) , 'data.document.subject.data.uCreator': this.userSesion.data.idens[0].number });
      }

      console.log('res_0000000',res);
      

    //  res = await this._apiMongo.filter(env.COLLECTION.document, env.TABLE_SIS.external, { 'data.partyType': this._fun.enum(this.key_table) });

      if (this._fun.isEmpty(res.result)) {
        this.load = false;
        return;
      }

      this.list_main = res.result[0][env.TABLE_SIS.external];

      this.list = this.list_main;
/*       this.filter(); */
      this.orderBy.order = true;
      this.orderBy.key = '';
      this.orderByTable('key');

      this.load = false;
    } catch (error) {
      this.load = false;
      await this._fun.alertError(error);
    }
  }

  changeInput(value, index, type) {
    if (type == 'checkbox') {
      value = value.detail.checked;
    }

    if (index == -1) {
      this.optionFilter[1].value = value;
      this.optionFilter[2].value = value;
    } else this.optionFilter[index].value = value;
    this.filter();
  }

  filter() {
    this.list = this.list_main.filter(a => {

      let arrayBand = [];

      for (let index = 0; index < this.optionFilter.length; index++) {

        arrayBand.push(true);
        var itemf: any = this.optionFilter[index];
        if (itemf.type == 'string') {

          let arrayJSON = [];
          arrayJSON = itemf.key.split('.');

          if (arrayJSON.length > 1) {

            let valorA: any = JSON.parse(JSON.stringify(a));

            arrayJSON.forEach(fld => { valorA = valorA[fld]; });

            arrayBand[index] = valorA.toLowerCase().indexOf(itemf.value.toLowerCase()) != -1;
       

          } else {
            arrayBand[index] = a[itemf.key].toLowerCase().indexOf(itemf.value.toLowerCase()) != -1;
          }
        }

        if (itemf.type == 'boolean') {
          if (a[itemf.key]) arrayBand[index] = true;
          else arrayBand[index] = itemf.value;
        }

        if (index == (this.optionFilter.length - 1)) {
          return arrayBand[0] && (arrayBand[1] || arrayBand[2]);
        }
      }

    });
    this.orderList();
  }

  orderByTable(key) {
    if (this.orderBy.key == key) {
      this.orderBy.order = !this.orderBy.order;
    } else {
      this.orderBy.order = true;
    }
    this.orderBy.key = key;
    this.orderList();
  }

  orderList() {
    let listOrder = this._fun.sortJSON(this.list, this.orderBy.key, this.orderBy.order);
    this.list = listOrder.filter(item => true);
  }
  
  async changeStatus(data, status) {

    let alert = await this._fun.alertChangStatus(status);
    if (this._fun.isVarInvalid(alert)) return;

    try {
      let res: any = await this._apiMongo.changeStatus(env.COLLECTION.document, env.TABLE_SIS.external, data.key, status);
    
      
      this.getList();
      await this._fun.alertSucc(status ? env.MSG.SUC_ENABLED : env.MSG.SUC_DISABLED);
    } catch (error) {
      await this._fun.alertError(error);
    }
  }

  async getCred(data?) {

    if (this._fun.isEmpty(this.listDocType)) return;
    const modal = await this._mod.create({
      cssClass: 'style-get-cred',
      component: GetCredComponent,
      componentProps: {
        data
      }
    });

    modal.onDidDismiss().then(async (res: any) => {
    
      if (this._fun.isVarInvalid(res.data)) return;
      if (!this._fun.isVarInvalid(res.data.confirm)) this.getList();
    });
    return await modal.present();
  }

  async delete(data) {

    let alert = await this._fun.alertDelete();
    if (this._fun.isVarInvalid(alert)) return;

    try {
      await this._apiMongo.delete(env.COLLECTION.document, env.TABLE_SIS.external, data.key);
      this.getList();
      await this._fun.alertSucc(env.MSG.SUC_DELETE);
    } catch (error) {
      await this._fun.alertError(error);
    }
  }

  async form(data?) {

    if (this._fun.isEmpty(this.listDocType)) return;
    const modal = await this._mod.create({
      component: AddDocExternComponent,
      componentProps: {
        key_table: this.key_table,
        listDocType: this.listDocType
      }
    });

    modal.onDidDismiss().then(async (res: any) => {

      if (this._fun.isVarInvalid(res.data)) return;
      if (!this._fun.isVarInvalid(res.data.confirm)) this.getList();
    });
    return await modal.present();
  }

  onChangePage(pageOfItems: Array<any>) {
    this.pageOfItems = pageOfItems;
  }
}
