  segmentChanged(value) {
    if (this.load) return;// Corrigue erroes al cambiar de tabla rapidamente
    this.key_table = value.detail.value;
    this.list = [];
    this.getList();
  }

  async getList() {
    this.load = true;
    try {
      let res: any;
      if (this.userSesion.data.role.key == 'MASTER') {
        res = await this._apiMongo.get(env.COLLECTION.party, this.key_table, 'items');
      } else {
        res = await this._apiMongo.filter(env.COLLECTION.party, this.key_table, { 'data.uCreator': this.userSesion.data.idens[0].number });
      }

      console.log('res', res);
      if (this._fun.isEmpty(res.result)) {
        this.load = false;
        return;
      }

      this.list_main = res.result[0][this.key_table];
      this.list = this.list_main;
      console.log('LISTAAA', this.list);

      this.filter();
      this.orderBy.order = true;
      this.orderBy.key = '';
      this.orderByTable('key');

      this.load = false;
    } catch (error) {
      this.load = false;
      await this._fun.alertError(error);
    }
  }

