import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Funciones } from '../compartido/funciones';
import { AuthService } from '../services/auth.service';
import { DataService } from '../services/data-service.service';
@Component({
  selector: 'app-pages',
  templateUrl: './pages.page.html',
  styleUrls: ['./pages.page.scss'],
})
export class PagesPage implements OnInit {

  userSesion: any;
  constructor(

    private _dataService: DataService,
    private _fun: Funciones,
    private authService: AuthService) { }



  appPages = [];

  options = [
    {
      "name": "Lista de QR",
      url: '/list-qr',
      code: 'qrs',
      icon: 'archive-outline',
      "modules": []
    },
    {
      "name": "Usuarios",
      url: '/users',
      code: 'use',
      icon: 'people',
      "modules": []
    },
    {
      "name": "Roles",
      url: '/roles',
      code: 'rol',
      icon: 'shield-half-outline',
      "modules": []
    },
    {
      "name": "Doc. externos",
      url: '/doc-ext',
      code: 'docex',
      icon: 'layers-outline',
      "modules": []
    },

    {
      "name": "Configuración",
      url: '/config',
      code: 'conf',
      icon: 'build-outline',
      "modules": [
        { name: 'Pantallas', url: '/pantalla', code: 'scr', icon: 'today-outline' },
        { name: 'Acciones', url: '/acciones', code: 'acc', icon: 'repeat-outline' },
        { name: 'Parámetros', url: '/param', code: 'par', icon: 'apps' }
      ]
    }
  ];

  sections = [
    { title: 'Administración', code: 'section' },
    { title: 'Configuración', code: 'section' }
  ];

  role: any;
  menuClose = false;

  ngOnInit() {
    this.getUser();
    this._dataService.eventBtnMn().subscribe(data => {
      if (this._fun.isVarInvalid(data)) return;
      console.log('data', data);
      this.menuClose = !this.menuClose;
    });
  }



  async getUser() {
    this.role = await this.authService.loadUser();
    let data = this.role.data.conf_web;

    //Unica Lista
    let uList = [];
    for (let index = 0; index < data.length; index++) {
      const mod = data[index];
      uList.push(...mod.modules);
    }

    console.log('LISTA UNICA', uList);

    for (let x = 0; x < this.options.length; x++) {
      const opt = this.options[x];
      if (opt.code == 'conf') {

        let confModules = [];
        let cf1 = uList.filter(m => m.code == 'scr' && m.value);//Pantalla
        let cf2 = uList.filter(m => m.code == 'acc' && m.value);//Acciones
        let cf3 = uList.filter(m => m.code == 'par' && m.value);//Parametros
        
        if (!this._fun.isEmpty(cf1)) confModules.push({ name: 'Pantallas', url: '/pantalla', code: 'scr', icon: 'today-outline' });
        if (!this._fun.isEmpty(cf2)) confModules.push({ name: 'Acciones', url: '/acciones', code: 'acc', icon: 'repeat-outline' });
        if (!this._fun.isEmpty(cf3)) confModules.push({ name: 'Parámetros', url: '/param', code: 'par', icon: 'apps' });
     
        if(confModules.length>0){
          this.appPages.push( {
            "name": "Configuración",
            url: '/config',
            code: 'conf',
            icon: 'build-outline',
            "modules": confModules
          });
        }
      }else{
        let mdOpc = uList.filter(m => m.code == opt.code && m.value);
        if (!this._fun.isEmpty(mdOpc)) this.appPages.push(opt);
      }

    }

  }


  /*  async getUser() {
     this.role = await this.authService.loadUser();

     let data = this.role.data.conf_web;
     console.log('data', data);

     for (let x = 0; x < data.length; x++) {
       for (let y = 0; y < data[x].modules.length; y++) {
         let md = data[x].modules[y];
         let mdOpc = this.options.filter(m => m.code == md.code && md.value);
         if (y == 0 && md.value) this.appPages.push(this.sections[x]);
         if (!this._fun.isEmpty(mdOpc)) this.appPages.push(mdOpc[0]);
       }

       if ((x + 1) == data.length) {
         this.appPages.push({ title: 'Mi usuario', code: 'section' });
         this.appPages.push({ title: 'Perfil', code: 'pro', url: '/profile', icon: 'person-circle' });
       }
     }

   } */
}
